<template>
  <div ref="chart" style="width: 100%; height: 400px;"></div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  name: 'PorkPriceChart',
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      // 获取 DOM 元素
      const chartDom = this.$refs.chart;
      const myChart = echarts.init(chartDom);
      
      // 数据：2015年每个月的猪肉价格（示例数据）
      const months = [
        '1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'
      ];
      const porkPrices = [
        22.5, 23.1, 23.4, 24.0, 24.7, 25.1, 26.0, 26.3, 27.2, 28.1, 29.0, 29.5
      ];
      
      // 配置图表选项
      const option = {
        title: {
          text: '2015年猪肉价格',
          subtext: '单位：元/公斤',
          left: 'center'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        xAxis: {
          type: 'category',
          data: months,
          axisTick: {
            alignWithLabel: true
          }
        },
        yAxis: {
          type: 'value',
          name: '价格 (元/公斤)'
        },
        series: [
          {
            name: '猪肉价格',
            type: 'bar',
            data: porkPrices,
            barWidth: '60%',
            itemStyle: {
              color: '#ff7f50'
            }
          }
        ]
      };

      // 使用指定的配置项和数据显示图表
      myChart.setOption(option);
    }
  }
};
</script>

